<template>
  <div class="container">
    <el-row type="flex" justify="center">
      <span class="title">请选择支付方式</span>
    </el-row>

    <el-row type="flex" justify="center">
      <div class="float_left">
        <div class="btn_container">
          <router-link :to="{name: 'payment/alipay'}">
            <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/payments/payment_alipay.png') + ')' }"></img-button>
          </router-link>
        </div>
      </div>
      <div class="float_left">
        <div class="btn_container">
          <router-link :to="{name: 'payment/weixin'}">
            <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/payments/payment_weixin.png') + ')' }"></img-button>
          </router-link>
        </div>
      </div>
      <div class="float_left">
        <div class="btn_container">
          <router-link :to="{name: 'payment/member'}">
            <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/payments/payment_member.png') + ')' }"></img-button>
          </router-link>
        </div>
      </div>
    </el-row>

    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
      </el-col>
      <el-col :span="8">
        <router-link :to="{name: 'extension/order'}">
          <el-button type="primary" class="btn_large">返回</el-button>
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  methods: {
    onSubmit() {
      this.$message('submit!')
    },
    onCancel() {
      this.$message({
        message: 'cancel!',
        type: 'warning'
      })
    }
  }
}
</script>

<style scoped>
.float_left {
  float: left;
}

.btn_container {
  margin-left: 80px;
}

.btn_huge {
  width: 240px;
  height: 360px;
  font-size: 32px;
}
</style>



